      ],
      // selectedCategoryIndex: -1, // üî• Êîπ‰∏∫ -1ÔºåË°®Á§∫Êú™ÈÄâ‰∏≠‰ªª‰ΩïÂàÜÁ±ª
      categoodsList: [], // ÂïÜÂìÅÂàóË°®
      selectedLocation: 'ÈóµË°å', // üîß Êñ∞Â¢ûÔºöÈªòËÆ§‰ΩçÁΩÆ
      showLocationModal: false, // üîß Êñ∞Â¢ûÔºö‰ΩçÁΩÆÈÄâÊã©ÂºπÁ™óÁä∂ÊÄÅ
      isUsingRandomMode: false, // üî• Ê†áËÆ∞ÊòØÂê¶Â∑≤ÂàáÊç¢Âà∞ÈöèÊú∫Ê®°Âºè
