		 // üî• Êñ∞Â¢û:ÊúÄÊñ∞ÂïÜÂìÅÁõ∏ÂÖ≥Êï∞ÊçÆ
		recentGoodsList: [],      // ÊúÄÊñ∞ÂïÜÂìÅÂàóË°®
