		recentIsLoading: false,    // ÊòØÂê¶Ê≠£Âú®Âä†ËΩΩ
		recentLoadedIds: new Set(), // Â∑≤Âä†ËΩΩÁöÑÂïÜÂìÅID
		// üî• Êñ∞Â¢û:‰æßËæπËèúÂçïÁõ∏ÂÖ≥
		showSideMenu: false, // ÊéßÂà∂ËèúÂçïÊòæÁ§∫/ÈöêËóè
		menuItems: [
