		  { name: 'Êàë‰π∞Âà∞ÁöÑ', icon: 'üõçÔ∏è', route: '/subpkg/my-bought/my-bought' },
		  { name: 'ÊàëÂçñÂá∫ÁöÑ', icon: 'üí∞', route: '/subpkg/my-sold/my-sold' },
		  { name: 'Êî∂ËóèÁöÑÂïÜÂìÅ', icon: '‚≠ê', route: '/subpkg/collect/collect' },
		  { name: 'ÊµèËßàË∂≥Ëøπ', icon: 'üë£', route: '/subpkg/history/history' },
		  { name: 'ËØÑ‰ª∑ÁöÑÂïÜÂìÅ', icon: 'üí¨', route: '/subpkg/comment/comment' }
