		excludeGoodsIds: [], // ‚úÖ Êñ∞Â¢ûÔºöÂ∑≤Ëé∑ÂèñÁöÑÂïÜÂìÅIDÂàóË°®
    };
  },
  
  methods: {
    ...mapMutations('m_user', ['updateUserInfo', 'updateToken', 'updateUserBase']),
    // üî• Êñ∞Â¢û:ÊâìÂºÄ‰æßËæπËèúÂçï
