      closeSideMenu() {
        this.showSideMenu = false;
      },
      
      // üî• Êñ∞Â¢û:Â§ÑÁêÜËèúÂçïÈ°πÁÇπÂáª
      handleMenuClick(item) {
        this.closeSideMenu();
        
        // Âª∂ËøüË∑≥ËΩ¨,ËÆ©ÂÖ≥Èó≠Âä®ÁîªÂÆåÊàê
        setTimeout(() => {
          uni.navigateTo({
            url: item.route,
            fail: (err) => {
              uni.showToast({
                title: 'È°µÈù¢ÂºÄÂèë‰∏≠',
                icon: 'none',
                duration: 2000
              });
            }
          });
        }, 300);
      },
	// üî• Êñ∞Â¢ûÔºöÈÄâÊã©Á±ªÂà´ÈÄâÈ°π
	    async selectCategoryOption(index) {
	      this.selectedCategoryIndex = index
	      
	      if (index === 0) {
	        // ÈÄâÊã©Êé®ËçêÔºöÊòæÁ§∫Êé®ËçêÂïÜÂìÅ
	        this.showCategoryGoods = false
	      } else {
	        // ÈÄâÊã©ÂÖ∑‰ΩìÂàÜÁ±ªÔºöÂä†ËΩΩÂàÜÁ±ªÂïÜÂìÅ
	        this.showCategoryGoods = true
	        this.pageNum = 1
	        this.categoodsList = []
	        this.hasMore = true
	        
	        await this.loadGoodsByCategory()
	        
	      }
	    },
		
	  // üî• ‰øÆÊîπÔºöÂ§ÑÁêÜÈ°µÈù¢ÊªöÂä®
	     handlePageScroll(e) {
	       const scrollTop = e.scrollTop
	       const delta = scrollTop - this.lastScrollTop
	       
	       // ÊéßÂà∂È°∂ÈÉ®ÂØºËà™Ê†è
	       if (delta > 0 && scrollTop > this.scrollThreshold) {
	         if (!this.isNavHidden) {
	           this.isNavHidden = true
	           this.navTransform = 'translateY(-100%)'
	         }
	       } else if (delta < 0) {
	         if (this.isNavHidden) {
	           this.isNavHidden = false
	           this.navTransform = 'translateY(0)'
	         }
	       }
	       
	       // üî• Êñ∞Â¢ûÔºöÊéßÂà∂Á±ªÂà´ÈÄâÈ°πÊ†èÔºàÂè™Âú®Êé®ËçêÈ°µÊòæÁ§∫Ôºâ
	       if (this.active === 0) {
	         if (scrollTop <= 10) {
	           // ÂõûÂà∞È°∂ÈÉ®ÔºöÊòæÁ§∫Á±ªÂà´Ê†è
	           this.isCategoryHidden = false
	           this.categoryTransform = 'translateY(0)'
	         } else if (delta > 0) {
	           // Âêë‰∏ãÊªöÂä®ÔºöÈöêËóèÁ±ªÂà´Ê†è
	           this.isCategoryHidden = true
	           this.categoryTransform = 'translateY(-100%)'
	         }
	       }
	       
	       this.lastScrollTop = scrollTop
	     },
	    
	    // üî• ‰øÆÊîπÔºöÈáçÁΩÆÂØºËà™Ê†èÁä∂ÊÄÅ
	        resetNavBar() {
	          this.isNavHidden = false
	          this.navTransform = 'translateY(0)'
	          this.isCategoryHidden = false
	          this.categoryTransform = 'translateY(0)'
	          this.lastScrollTop = 0
	        },
		
    // üî• Êñ∞Â¢ûÔºöË∑≥ËΩ¨Âà∞Ê∂àÊÅØÈ°µÈù¢
    gotoMessage() {
		if (!this.openid) {
			uni.switchTab({
		  url: '/pages/my/my'
		})
		return
		}
		
      uni.navigateTo({
        url: '/pages/message/message'
      });
    },
      
    // üî• Êñ∞Â¢ûÔºöÂàáÊç¢ÂàÜÁ±ª‰∏ãÊãâËèúÂçï
    toggleCategoryDropdown() {
      this.showCategoryDropdown = !this.showCategoryDropdown;
    },
      
    // üî• Êñ∞Â¢ûÔºöÂÖ≥Èó≠ÂàÜÁ±ª‰∏ãÊãâËèúÂçï
    closeCategoryDropdown() {
      this.showCategoryDropdown = false;
    },
    
    // üî• Êñ∞Â¢ûÔºö‰ªé‰∏ãÊãâËèúÂçïÈÄâÊã©ÂàÜÁ±ª
   // üî• ‰øÆÊîπÔºö‰ªé‰∏ãÊãâËèúÂçïÈÄâÊã©ÂàÜÁ±ª
       async selectCategoryFromDropdown(index) {
         this.selectedCategoryIndex = index;
         this.showCategoryDropdown = false;
         this.showCategoryGoods = true;
         
         // ÈáçÁΩÆÂàÜÈ°µ
         this.pageNum = 1;
         this.categoodsList = [];
         this.hasMore = true;
         
         // Âä†ËΩΩÂàÜÁ±ªÂïÜÂìÅ
         await this.loadGoodsByCategory();
         
         uni.showToast({
           title: `Â∑≤ÂàáÊç¢Âà∞ ${this.currentCategoryName}`,
           icon: 'none',
           duration: 1500
         });
       },
    
    // üî•üî•üî• ÈÄâÊã©ÂàÜÁ±ª üî•üî•üî•
    async selectCategory(index) {
          // üî• ÁßªÈô§ËøôË°åÂà§Êñ≠ÔºåÂÖÅËÆ∏ÈáçÂ§çÁÇπÂáªÂêå‰∏ÄÂàÜÁ±ª
          // if (this.selectedCategoryIndex === index) return
          
          this.selectedCategoryIndex = index;
          this.pageNum = 1;
          this.categoodsList = [];
          this.hasMore = true;
          this.showCategoryGoods = true;
          
          await this.loadGoodsByCategory();
          
        },
    
    // üî•üî•üî• Âä†ËΩΩÂàÜÁ±ªÁªüËÆ°Êï∞ÊçÆ üî•üî•üî•
    async loadCategoryStats() {
      try {
        const { data: res } = await uni.$http.get('/goods/categorystats')
        
        if (res.meta.status === 200) {
          // Êõ¥Êñ∞ÊØè‰∏™ÂàÜÁ±ªÁöÑÂïÜÂìÅÊï∞Èáè
          res.message.forEach(stat => {
            const category = this.categoryList.find(c => c.name === stat.category)
            if (category) {
              category.count = stat.count
            }
          })
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂàÜÁ±ªÁªüËÆ°Â§±Ë¥•:', error)
      }
    },
    
   // üî• Ê†πÊçÆÂàÜÁ±ªÂä†ËΩΩÂïÜÂìÅ
      // üî• Ê†πÊçÆÂàÜÁ±ªÂä†ËΩΩÂïÜÂìÅÔºàÈöèÊú∫Ê®°ÂºèÔºâ
        async loadGoodsByCategory() {
          if (this.isLoading || !this.hasMore) return
          
          this.isLoading = true
          
          try {
            const categoryName = this.currentCategoryName
            
            const { data: res } = await uni.$http.get('/goods/categorylist', {
              category: categoryName,
              pageNum: this.pageNum,
              pageSize: this.pageSize,
              excludeGoodsIds: this.excludeGoodsIds.join(','), // ‚úÖ ‰º†ÈÄíÊéíÈô§ÂàóË°®
              randomMode: 'true' // ‚úÖ ÂêØÁî®ÈöèÊú∫Ê®°Âºè
            })
            
            if (res.meta.status === 200) {
              const newGoods = res.message.goods || []
              
              // ‚úÖ Á¥ØÁßØÂ∑≤Ëé∑ÂèñÁöÑÂïÜÂìÅID
              if (res.message.fetchedGoodsIds && res.message.fetchedGoodsIds.length > 0) {
                this.excludeGoodsIds = [...this.excludeGoodsIds, ...res.message.fetchedGoodsIds]
                console.log(`‚úÖ Á¥ØÁßØÂ∑≤Ëé∑ÂèñÂïÜÂìÅID: ${this.excludeGoodsIds.length} ‰∏™`)
              }
              
              if (this.pageNum === 1) {
                this.categoodsList = newGoods
              } else {
                this.categoodsList = [...this.categoodsList, ...newGoods]
              }
              
              // ‚úÖ ‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑ hasMore Âà§Êñ≠
              this.hasMore = res.message.hasMore === true
              
              console.log(`‚úÖ Âä†ËΩΩÊàêÂäü - Êñ∞Â¢û: ${newGoods.length}, ÊÄªËÆ°: ${this.categoodsList.length}`)
            }
          } catch (error) {
            console.error('Âä†ËΩΩÂïÜÂìÅÂ§±Ë¥•:', error)
            uni.showToast({
              title: 'Âä†ËΩΩÂ§±Ë¥•',
              icon: 'none'
            })
          } finally {
            this.isLoading = false
          }
        },
    
    // üîß Êñ∞Â¢ûÔºöÊòæÁ§∫‰ΩçÁΩÆÈÄâÊã©Âô®
    showLocationPicker() {
      this.showLocationModal = true
    },

    // üîß Êñ∞Â¢ûÔºöÈöêËóè‰ΩçÁΩÆÈÄâÊã©Âô®
