      this.selectedLocation = location
      this.hideLocationPicker()
      
      uni.showToast({
        title: `Â∑≤ÈÄâÊã©${location}Ê†°Âå∫`,
        icon: 'success',
        duration: 1000
      })
    },
	
	// üî• ÂàáÊç¢ÂàÜÁ±ªÊó∂ÈáçÁΩÆ
