    // ‚úÖ Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(timestamp) {
      if (!timestamp) return '';
      const now = Math.floor(Date.now() / 1000);
      const diff = now - timestamp;
      
      if (diff < 60) return 'ÂàöÂàö';
      if (diff < 3600) return Math.floor(diff / 60) + 'ÂàÜÈíüÂâç';
      if (diff < 86400) return Math.floor(diff / 3600) + 'Â∞èÊó∂Ââç';
      if (diff < 604800) return Math.floor(diff / 86400) + 'Â§©Ââç';
      
      const date = new Date(timestamp * 1000);
      return `${date.getMonth() + 1}-${date.getDate()}`;
    },
    
    gotoSearch() {
		if (!this.openid) {
			uni.switchTab({
		  url: '/pages/my/my'
		})
		return
		}
      uni.navigateTo({
        url: '/subpkg/search/search'
      })
    },
    
    async activeChanged(i) {
		 this.resetNavBar() // ÂàáÊç¢Ê†áÁ≠æÊó∂ÈáçÁΩÆÂØºËà™Ê†è
      try {
        this.active = i;
        // üî• ÂàáÊç¢Ê†áÁ≠æÊó∂ÈöêËóèÂàÜÁ±ªÂïÜÂìÅ
		 // üî• ÂÖ≥ÈîÆ‰øÆÊîπÔºöÂ¶ÇÊûúÁÇπÂáªÁöÑÊòØÂàÜÁ±ªÈÄâÈ°π(index=2)
		 
		 // üî• Â¶ÇÊûúÁÇπÂáªÁöÑÊòØ"Êñ∞ÂìÅ"Ê†áÁ≠æ(index=1)
		     if (i === 1) {
		       if (!this.openid) {
		         this.active = 0;
		         uni.switchTab({
		           url: '/pages/my/my'
		         });
		         return;
		       }
		       
		       // üî• Â¶ÇÊûúÊòØÁ¨¨‰∏ÄÊ¨°Âä†ËΩΩÊñ∞ÂìÅ,ÂàôËØ∑Ê±ÇÊï∞ÊçÆ
		       if (this.recentGoodsList.length === 0) {
		         await this.getRecentGoodsList();
		       }
		       return;
		     }
			 
		if (i === 2) {
		  // üî• Â¶ÇÊûú‰πãÂâçÂ∑≤ÁªèÈÄâ‰∏≠‰∫ÜÊüê‰∏™ÂàÜÁ±ªÔºåÊ≠§Êó∂Â∫îËØ•ÈáçÁΩÆÁä∂ÊÄÅÔºåÊòæÁ§∫ÂàÜÁ±ªÈÄâÊã©È°µÈù¢
		  if (this.showCategoryGoods) {
			 this.showCategoryGoods = false
			      this.selectedCategoryIndex = 0
			
			uni.showToast({
			  title: 'ËØ∑ÈÄâÊã©ÂàÜÁ±ª',
			  icon: 'none',
			  duration: 1500
			});
		  }
		  return;
		}
		// üî• ÂàáÊç¢Âà∞ÂÖ∂‰ªñÊ†áÁ≠æÊó∂ÔºåÈöêËóèÂàÜÁ±ªÂïÜÂìÅÂπ∂ÈáçÁΩÆÈÄâ‰∏≠Áä∂ÊÄÅ
		       this.showCategoryGoods = false
		            this.selectedCategoryIndex = 0
				
        if (this.active === 2) {
          // ÂàÜÁ±ªÈ°µÈù¢‰∏çÈúÄË¶ÅÁâπÊÆäÂ§ÑÁêÜ
          return;
        }

        if (this.active === 1) {
          if (!this.openid) {
            this.active = 0
            uni.switchTab({
              url: '/pages/my/my'
            })
            return;
          }
          const queryObj = { code: this.openid };
          const { data: res } = await uni.$http.get('/users/userinfo', queryObj);

          if (res.meta.status === 200) {
            this.updateUserBase(res.message);
          }

          const followingIds = res.message.following_ids;

          if (!Array.isArray(followingIds) || followingIds.length === 0) {
            this.followers = [];
            this.filteredgoodsList = [];
            return;
          }

          const followerPromises = followingIds.map(async (userId) => {
            try {
              const queryObj1 = { code: userId };
              const { data: res1 } = await uni.$http.get('/users/userinfo', queryObj1);
              if (res1.meta.status === 200) {
                return res1.message;
              } else {
                return null;
              }
            } catch (error) {
              return null;
            }
          });

          const followersResults = await Promise.all(followerPromises);
          this.followers = followersResults.filter(f => f !== null);

          this.goods_ids = [];
          
          if (Array.isArray(this.followers) && this.followers.length > 0) {
            this.followers.forEach(follower => {
              if (Array.isArray(follower?.goods_id)) {
                this.goods_ids.push(...follower.goods_id.map(id => String(id)));
              } else if (follower?.goods_id) {
                this.goods_ids.push(String(follower.goods_id));
              }
            });
          } else {
            this.filteredgoodsList = [];
            return;
          }

          if (this.goods_ids.length === 0) {
            this.filteredgoodsList = [];
            return;
          }
          
          const queryObj2 = { goods_id: this.goods_ids };
          const { data: res2 } = await uni.$http.get('/goods/historysearch', queryObj2);

          if (res2.meta.status === 200) {
            this.filteredgoodsList = res2.message;
          } else {
            uni.showToast({
              title: 'Ëé∑ÂèñÂïÜÂìÅ‰ø°ÊÅØÂ§±Ë¥•',
              icon: 'none'
            });
            this.filteredgoodsList = [];
          }
        }
      } catch (error) {
        console.error('activeChanged ÊñπÊ≥ïÊâßË°åÂá∫Èîô:', error);
        uni.showToast({
          title: 'Êìç‰ΩúÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï',
          icon: 'none'
        });
      }
    },
    
	 /**
	   * üî• Ëé∑ÂèñÊúÄÊñ∞ÂïÜÂìÅÂàóË°®(72Â∞èÊó∂ÂÜÖ)
	   * @param {Function} cb ÂõûË∞ÉÂáΩÊï∞
	   */
	 async getRecentGoodsList(cb) {
	   if (this.recentIsLoading || !this.recentHasMore) {
	     cb && cb();
	     return;
	   }
	   
	   this.recentIsLoading = true;
	   
	   try {
	     const queryObj = {
	       pagesize: this.recentPageSize,
	       excludeIds: JSON.stringify(Array.from(this.recentLoadedIds))
	     };
	     // ‚úÖ ‰∏çÂÜç‰º†ÈÄí pagenum
	     
	     console.log(`üì¶ Âä†ËΩΩÊúÄÊñ∞ÂïÜÂìÅ - Â∑≤Âä†ËΩΩ:${this.recentLoadedIds.size}‰ª∂`);
	     
	     const { data: res } = await uni.$http.get('/goods/recentlist', queryObj);
	     
	     if (res.meta.status === 200) {
	       const newGoods = res.message.goods || [];
	       
	       // üî• ÂéªÈáçÔºàËôΩÁÑ∂ÂêéÁ´ØÂ∑≤Â§ÑÁêÜÔºå‰ΩÜÂâçÁ´ØÂÜçÊ¨°Á°ÆËÆ§Êõ¥ÂÆâÂÖ®Ôºâ
	       const uniqueNewGoods = newGoods.filter(
	         goods => !this.recentLoadedIds.has(goods.goods_id)
	       );
	       
	       // üî• Ê∑ªÂä†Âà∞ÂàóË°®
	       this.recentGoodsList = [...this.recentGoodsList, ...uniqueNewGoods];
	       
	       // üî• ËÆ∞ÂΩïÂ∑≤Âä†ËΩΩÁöÑID
	       uniqueNewGoods.forEach(goods => this.recentLoadedIds.add(goods.goods_id));
	       
	       // üî• Êõ¥Êñ∞ÊòØÂê¶ËøòÊúâÊõ¥Â§ö
	       this.recentHasMore = res.message.hasMore;
	       
	       console.log(`‚úÖ ÊàêÂäüÂä†ËΩΩ ${uniqueNewGoods.length} ‰ª∂ÊúÄÊñ∞ÂïÜÂìÅÔºåÊÄªËÆ° ${this.recentGoodsList.length} ‰ª∂`);
	       
	       if (!this.recentHasMore) {
	       }
	     } else {
	       uni.showToast({
	         title: res.meta.msg || 'Âä†ËΩΩÂ§±Ë¥•',
	         icon: 'none'
	       });
	     }
	   } catch (error) {
	     console.error('‚ùå Ëé∑ÂèñÊúÄÊñ∞ÂïÜÂìÅÂ§±Ë¥•:', error);
	     uni.showToast({
	       title: 'ÁΩëÁªúÈîôËØØ',
	       icon: 'none'
	     });
	   } finally {
	     this.recentIsLoading = false;
	     cb && cb();
	   }
	 },
	  
    async getGoodsList(cb) {
        this.isloading = true
        
        // üî• Ê†πÊçÆÊ®°ÂºèÈÄâÊã©‰∏çÂêåÁöÑÊé•Âè£
        const endpoint = this.isUsingRandomMode ? '/goods/list' : '/goods/suggestionsearch'
        const queryObj = {
          openid: this.openid,
          pagenum: this.queryObj.pagenum,
          pagesize: 6
        };
        
        console.log(`Ë∞ÉÁî®Êé•Âè£: ${endpoint}, È°µÁ†Å: ${this.queryObj.pagenum}, Ê®°Âºè: ${this.isUsingRandomMode ? 'ÈöèÊú∫' : 'Êé®Ëçê'}`)
        
        const { data: res } = await uni.$http.get(endpoint, queryObj)
        this.isloading = false
        cb && cb()
        
        if (res.meta.status !== 200) return uni.$showMsg()
        
        const newGoods = res.message.goods || []
        
        // üî• ÂéªÈáçÈÄªËæë:‰ΩøÁî®ÂÖ®Â±ÄÁöÑ loadedGoodsIds ÂéªÈáç
        const uniqueNewGoods = newGoods.filter(goods => !this.loadedGoodsIds.has(goods.goods_id))
        
        // üî• ÂÖ≥ÈîÆÂà§Êñ≠:Â¶ÇÊûúÊé®ËçêÊ®°Âºè‰∏ãÊ≤°ÊúâÊñ∞ÂïÜÂìÅ,ÂàáÊç¢Âà∞ÈöèÊú∫Ê®°Âºè
        if (!this.isUsingRandomMode && uniqueNewGoods.length === 0 && newGoods.length > 0) {
          console.log('‚ö†Ô∏è Êé®ËçêÂïÜÂìÅÂ∑≤ÂÖ®ÈÉ®Âä†ËΩΩ,ÂàáÊç¢Âà∞ÈöèÊú∫ÂïÜÂìÅÊ®°Âºè')
          this.isUsingRandomMode = true
          
          // üî• ÂàáÊç¢Ê®°ÂºèÂêéÁ´ãÂç≥ËØ∑Ê±ÇÈöèÊú∫ÂïÜÂìÅ
          this.queryObj.pagenum = 1 // ÈáçÁΩÆÈ°µÁ†Å
          await this.getGoodsList(cb)
          return
        }
        
        // üî• Ê∑ªÂä†Êñ∞ÂïÜÂìÅÂà∞ÂàóË°®
        this.goodsList = [...this.goodsList, ...uniqueNewGoods]
        
        // üî• Êõ¥Êñ∞Â∑≤Âä†ËΩΩÂïÜÂìÅIDÈõÜÂêà
        uniqueNewGoods.forEach(goods => this.loadedGoodsIds.add(goods.goods_id))
        
        this.total = res.message.total
        
		        if (this.isUsingRandomMode && uniqueNewGoods.length === 0) {
		          uni.showToast({
		            title: 'ÊöÇÊó†Êõ¥Â§öÂïÜÂìÅ',
		            icon: 'none',
		            duration: 2000
		          })
		        }
		      },
		
