			    const trySetTabBar = () => {
			      if (typeof this.$scope.getTabBar === 'function') {
			        const tabBar = this.$scope.getTabBar();
			        if (tabBar && tabBar.setData) {
			          tabBar.setData({
			            selected: index
			          });
			          return true;  // ËÆæÁΩÆÊàêÂäü
			        }
			      }
			      return false;  // ËÆæÁΩÆÂ§±Ë¥•
			    };
			    
			    // Á¨¨‰∏ÄÊ¨°Á´ãÂç≥ÊâßË°å
			    if (!trySetTabBar()) {
			      // Â¶ÇÊûúÂ§±Ë¥•,‰ΩøÁî®Áü≠Âª∂ËøüÈáçËØï
			      setTimeout(() => {
			        trySetTabBar();
			      }, 50);
			    }
			    // #endif
			  },
			  updateTabBar(index) {
			        // #ifdef MP-WEIXIN
			        // Á°Æ‰øùÊòØÂΩìÂâçÊøÄÊ¥ªÁöÑÈ°µÈù¢
			        const pages = getCurrentPages();
			        const currentPage = pages[pages.length - 1];
			        
			        // ‰ΩøÁî® this.$mp.page Êàñ currentPage Êù•Ê£ÄÊü•
			        if (currentPage && currentPage.route === 'pages/home/home') {
			          // ÊØèÊ¨°ÈÉΩÈáçÊñ∞Ëé∑Âèñ tabBar ÂÆû‰æã
			          if (typeof this.$scope.getTabBar === 'function') {
			            const tabBar = this.$scope.getTabBar();
