		  async onLoad(options) {
		    const currentTimestamp = Math.floor(Date.now() / 1000);
		    const sysInfo = uni.getSystemInfoSync()
		    this.sw = sysInfo.windowHeight
		    this.queryObj.query = ''
		    this.queryObj.cid = ''
		    if (this.active === 0) {
		      this.getGoodsList()
		    }
		    
		    if (this.active === 2 && options.category) {
		          const index = this.categoryList.findIndex(item => item.name === options.category);
		          if (index !== -1) {
		            this.selectedCategoryIndex = index;
		            this.showCategoryGoods = true;
		            await this.loadGoodsByCategory();
		          }
		        }
		    // Âä†ËΩΩÂàÜÁ±ªÁªüËÆ°Êï∞ÊçÆ
		    await this.loadCategoryStats()
		    // Âä†ËΩΩÂΩìÂâçÂàÜÁ±ªÁöÑÂïÜÂìÅ
		    // await this.loadGoodsByCategory()
		  },
		  
		  // üî• ‰øÆÊîπÔºöonShow - ÈáçÁΩÆÂØºËà™Ê†è
		    async onShow() {
			  this.updateTabBar(1);
		    },
		  
		  onReachBottom() {
			  // üî• Â¶ÇÊûúÂú®"Êñ∞ÂìÅ"Ê†áÁ≠æ,Âä†ËΩΩÊõ¥Â§öÊúÄÊñ∞ÂïÜÂìÅ
			    if (this.active === 1) {
			      if (!this.recentHasMore) {
			        return uni.showToast({
			          title: 'ÊöÇÊó†Êõ¥Â§öÊúÄÊñ∞ÂïÜÂìÅ',
			          icon: 'none'
			        });
			      }
			      
			      if (this.recentIsLoading) return;
			      
			      this.recentPageNum++;
			      this.getRecentGoodsList();
			      return;
			    }
			    
			    // üî• Â¶ÇÊûúÂú®"ÂàÜÁ±ª"Ê†áÁ≠æ,Âä†ËΩΩÊõ¥Â§öÂàÜÁ±ªÂïÜÂìÅ
			    if (this.active === 0 && this.currentCategoryName !== 'Êé®Ëçê') {
			      this.loadMore();
			      return;
			    }
				
		    if (this.queryObj.pagenum * this.queryObj.pagesize >= this.total)
		      return uni.$showMsg('Êï∞ÊçÆÂä†ËΩΩÂÆåÊØïÔºÅ')
		    if (this.isloading) return
		    this.queryObj.pagenum += 1
		    this.getGoodsList()
		  },
		  	
		  // üî• ‰øÆÊîπÔºöonPullDownRefresh - ÈáçÁΩÆÂØºËà™Ê†è
		    onPullDownRefresh() {
		      this.resetNavBar() // ‰∏ãÊãâÂà∑Êñ∞Êó∂ÈáçÁΩÆÂØºËà™Ê†è
		      
			  // üî• Â¶ÇÊûúÂú®"Êñ∞ÂìÅ"Ê†áÁ≠æ
			    if (this.active === 1) {
			      this.recentPageNum = 1;
			      this.recentGoodsList = [];
			      this.recentLoadedIds.clear();
			      this.recentHasMore = true;
			      this.getRecentGoodsList(() => uni.stopPullDownRefresh());
			      return;
			    }
			    
			    // üî• Â¶ÇÊûúÂú®"ÂàÜÁ±ª"Ê†áÁ≠æ
			    if (this.active === 0 && this.currentCategoryName !== 'Êé®Ëçê') {
			      this.pageNum = 1;
			      this.categoodsList = [];
				  this.excludeGoodsIds = [] // ‚úÖ ÈáçÁΩÆÊéíÈô§ÂàóË°®
			      this.hasMore = true;
			      this.loadGoodsByCategory().then(() => uni.stopPullDownRefresh());
			      return;
			    }
				
		      this.queryObj.pagenum = 1
		      this.total = 0
		      this.isloading = false
		      this.goodsList = []
